import React from 'react';
import bg from '../../style/img/p2_bg.jpg';
import p2_circle_outer from '../../style/img/p2_circle_outer.png';
import p2_circle_middle from '../../style/img/p2_circle_middle.png';
import p2_circle_inner from '../../style/img/p2_circle_inner.png';
import p2_2019 from '../../style/img/p2_2019.png';
import {isMobile} from "../../utils";

export default class index extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isVisible: false
        }
    }

    componentDidMount() {
        const {play} = this.props;
        setTimeout(() => {
            this.setState(() => ({
                isVisible: true
            }));
            play&&play(true)
        }, 5500)
    }

    render() {
        const {className} = this.props;
        return (
            <div id="page2"
                 className={"full-w full-h relative "+className}
                 style={{backgroundImage: `url(${bg})`, backgroundSize: '100% 100%'}}>
                <div className="bg p2_bg_loading"/>
                <div className="bg"/>
                <div className="p2_circle"/>
                <div className="p2_2018"/>
                <style>{
                    `
                        #page2 {
                            -webkit-transition: .5s;
                            transition: .5s
                        }

                        #page2.fadeOut {
                            opacity: .3;
                            -webkit-transform: translate(0, -100%);
                            transform: translate(0, -100%)
                        }

                        #page2 .bg {
                            background: url("../img/p2_bg.jpg") no-repeat center center
                        }

                        #page2 .p2_circle {
                            position: absolute;
                            top: 0;
                            right: 0;
                            bottom: 0;
                            left: 0;
                            margin: auto;
                            background: url(${p2_circle_outer}) no-repeat center center;
                            background-size: 100%;
                            width: ${isMobile()?'59.375vw':'222px'};
                            height: ${isMobile()?'59.375vw':'222px'};
                            -webkit-animation: p2_circle_outer 1s linear 3s infinite;
                            animation: p2_circle_outer 1s linear 3s infinite
                        }

                        #page2 .p2_circle:before {
                            position: absolute;
                            top: 0;
                            right: 0;
                            bottom: 0;
                            left: 0;
                            margin: auto;
                            content: "";
                            background: url(${p2_circle_middle}) no-repeat center center;
                            background-size: 100%;
                            width: ${isMobile()?'45.625vw':'171px'};
                            height: ${isMobile()?'45.625vw':'171px'};
                            -webkit-animation: p2_circle_middle 1s linear 2s infinite;
                            animation: p2_circle_middle 1s linear 2s infinite
                        }

                        #page2 .p2_circle:after {
                            position: absolute;
                            top: 0;
                            right: 0;
                            bottom: 0;
                            left: 0;
                            margin: auto;
                            content: "";
                            background: url(${p2_circle_inner}) no-repeat center center;
                            background-size: 100%;
                            width: ${isMobile()?'39.9375vw':'149px'};
                            height: ${isMobile()?'39.9375vw':'149px'};
                            -webkit-animation: p2_circle_inner 1s linear 1s infinite;
                            animation: p2_circle_inner 1s linear 1s infinite
                        }

                        #page2 .p2_2018 {
                            position: absolute;
                            top: 0;
                            right: 0;
                            bottom: 0;
                            left: 0;
                            margin: auto;
                            background: url(${p2_2019}) no-repeat center center;
                            background-size: 100%;
                            width:  ${isMobile()?'27.5vw':'103px'};
                            height: ${isMobile()?'7.5vh':'60px'};
                        }

                        #page2 .p2_bg_loading {
                            z-index: 4;
                            background: #EF1639;
                            -webkit-animation: p2_bg_loading 1s linear forwards;
                            animation: p2_bg_loading 1s linear forwards
                        }

                        @-webkit-keyframes p2_circle_inner {
                            0% {
                                -webkit-transform: rotate(0deg);
                                transform: rotate(0deg)
                            }
                            100% {
                                -webkit-transform: rotate(-1080deg);
                                transform: rotate(-1080deg)
                            }
                        }

                        @keyframes p2_circle_inner {
                            0% {
                                -webkit-transform: rotate(0deg);
                                transform: rotate(0deg)
                            }
                            100% {
                                -webkit-transform: rotate(-1080deg);
                                transform: rotate(-1080deg)
                            }
                        }

                        @-webkit-keyframes p2_circle_middle {
                            0% {
                                -webkit-transform: rotate(0deg);
                                transform: rotate(0deg)
                            }
                            100% {
                                -webkit-transform: rotate(-1080deg);
                                transform: rotate(-1080deg)
                            }
                        }

                        @keyframes p2_circle_middle {
                            0% {
                                -webkit-transform: rotate(0deg);
                                transform: rotate(0deg)
                            }
                            100% {
                                -webkit-transform: rotate(-1080deg);
                                transform: rotate(-1080deg)
                            }
                        }

                        @-webkit-keyframes p2_circle_outer {
                            0% {
                                -webkit-transform: rotate(0deg);
                                transform: rotate(0deg)
                            }
                            100% {
                                -webkit-transform: rotate(-720deg);
                                transform: rotate(-720deg)
                            }
                        }

                        @keyframes p2_circle_outer {
                            0% {
                                -webkit-transform: rotate(0deg);
                                transform: rotate(0deg)
                            }
                            100% {
                                -webkit-transform: rotate(-360deg);
                                transform: rotate(-360deg)
                            }
                        }

                        @-webkit-keyframes p2_bg_loading {
                            0% {
                                opacity: 1
                            }
                            100% {
                                opacity: 0
                            }
                        }

                        @keyframes p2_bg_loading {
                            0% {
                                opacity: 1
                            }
                            100% {
                                opacity: 0
                            }
                        }

                     `
                }</style>
            </div>
        )
    }
}
