import React from 'react';
import bg from '../../style/img/bg_4.png';
import p3_logo from '../../style/img/p3_logo.png';
import ll from '../../style/img/ll.png';
import p3_couplet_second from '../../style/img/p3_couplet_second.png';
import p3_couplet_first from '../../style/img/p3_couplet_first.png';
import p3_blessing from '../../style/img/p3_blessing.png';
import {isMobile} from "../../utils";

export default class index extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isVisible: false
        }
    }

    componentDidMount() {
        const {play} = this.props;
        // setTimeout(() => {
        //     this.setState(() => ({
        //         isVisible: true
        //     }));
        //     play&&play()
        // }, 5500)
    }

    render() {
        const {className} = this.props;
        return (
            <div id="page3"
                 className={"full-w full-h relative "+className}
                 style={{backgroundImage: `url(${bg})`, backgroundSize: '100% 100%'}}>
                <div className="p3_logo animated dflash"/>
                <div className="p3_title"/>
                <div className="p3_second"/>
                <div className="p3_first"/>
                <div className="p3_blessing"/>
                <style>{
                    `@-webkit-keyframes flash {
                          from, 50%, to {
                            opacity: 1;
                          }

                          25%, 75% {
                            opacity: 0;
                          }
                        }

                        @keyframes flash {
                          from, 50%, to {
                            opacity: 1;
                          }

                          25%, 75% {
                            opacity: 0;
                          }
                        }

                        .dflash {
                          -webkit-animation-name: flash;
                          animation-name: flash;
                          animation-iteration-count:5;
                        }
                        #page3 {
                            -webkit-transition: .5s;
                            transition: .5s
                        }

                        #page3.fadeIn {
                            -webkit-transform: translate(0, -100%);
                            transform: translate(0, -100%)
                        }

                        #page3 .bg {
                            /*background: url("../img/p4_bg.png") no-repeat center center*/
                            background: url("../img/p3_bg.jpg") no-repeat center center
                        }

                        #page3 .p3_logo {
                            width: ${isMobile()?'34.6875vw':'130px'};
                            height: ${isMobile()?'6.327vh':'51px'};;
                            position: absolute;
                            top: 7.82vh;
                            right: 0;
                            left: 0;
                            margin: auto;
                            background: url(${p3_logo}) no-repeat center center;
                            background-size: 100%
                        }

                        #page3 .p3_title {
                            width: ${isMobile()?'48.125vw':'180px'};
                            height: ${isMobile()?'50vh':'406px'};
                            position: absolute;
                            top: 2vh;
                            left: 0;
                            right: 0;
                            margin: auto;
                            background: url(${ll}) no-repeat center center;
                            background-size: 100%
                        }

                        #page3 .p3_second {
                             width: ${isMobile()?'22.8125vw':'85px'};
                            height: 41.652vh;
                            position: absolute;
                            top: 25.48vh;
                            left: 3.75vw;
                            background: url(${p3_couplet_second}) no-repeat center center;
                            background-size: 100%
                        }

                        #page3 .p3_first {
                            width: ${isMobile()?'22.8125vw':'85px'};
                            height: 41.652vh;
                            position: absolute;
                            top: 25.48vh;
                            right: 3.75vw;
                            background: url(${p3_couplet_first}) no-repeat center center;
                            background-size: 100%
                        }

                        #page3 .p3_blessing {
                            width: ${isMobile()?'32vw':'120px'};
                            height: ${isMobile()?'32vw':'120px'};
                            position: absolute;
                            right: 0;
                            left: 0;
                            bottom: 10vh;
                            margin: auto;
                            border-radius: 50%;
                            background: url(${p3_blessing}) no-repeat center center;
                            background-size: 100%;
                            -webkit-animation: p3_plessing 2s linear infinite;
                            animation: p3_plessing 2s linear infinite
                        }

                        @-webkit-keyframes p3_plessing {
                            0% {
                                -webkit-transform: rotate(0deg);
                                transform: rotate(0deg)
                            }
                            100% {
                                -webkit-transform: rotate(360deg);
                                transform: rotate(360deg)
                            }
                        }

                        @keyframes p3_plessing {
                            0% {
                                -webkit-transform: rotate(0deg);
                                transform: rotate(0deg)
                            }
                            100% {
                                -webkit-transform: rotate(360deg);
                                transform: rotate(360deg)
                            }
                        }

                     `
                }</style>
            </div>
        )
    }
}
