import React from 'react';
import bg from './p1_bg.jpg';
import p1_imooc from '../../style/img/p1_imooc.png';
import p1_lantern from '../../style/img/p1_lantern.png';
import {isMobile} from "../../utils";

export default class index extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isVisible: false
        }
    }

    componentDidMount() {
        setTimeout(() => {
            this.setState(() => ({
                isVisible: true
            }));
        }, 1000)
    }

    render() {
        const {play,className} = this.props;
        return (
            <div className={"full-w full-h relative "+className}
                 style={{backgroundImage: `url(${bg})`, backgroundSize: '100% 100%'}}>
                <div className="p1_lantern" onClick={play}><br/>2019</div>
                <div className="p1_words">2019</div>
                <style>{
                    `
                         .p1_lantern {
                            position: absolute;
                            top: -4.4%;
                            right: 0;
                            left: 0;
                            margin: auto;
                            width: 45%;
                            height: 72vh;
                            font-size: 22px;
                            z-index:7;
                            color: #fff;
                            background: url(${p1_lantern}) no-repeat center center;
                            background-size: 100%;
                            padding-top: 31vh;
                            -webkit-box-sizing: border-box;
                            box-sizing: border-box;
                            text-align: center
                        }
                        
                         .p1_lantern:before {
                            position: absolute;
                            top: -2.5%;
                            right: 0;
                            left: 0;
                            bottom: 0;
                            content: "";
                            z-index:-1;
                            margin: auto;
                            width: ${isMobile()?'31vw':'110px'};
                            height: ${isMobile()?'31vw':'110px'};
                            background-color: #d60b3b;
                            opacity: .5;
                            border-radius: 50%;
                            -webkit-box-shadow: 0 0 2vw 2vw #d60b3b;
                            box-shadow: 0 0 2vw 2vw #d60b3b;
                            -webkit-animation: p1_lantern .5s infinite alternate;
                            animation: p1_lantern .5s infinite alternate
                        }
                        
                         .p1_icon {
                            position: absolute;
                            right: 0;
                            left: 0;
                            bottom: 9vh;
                            background: url(${p1_imooc}) no-repeat center center;
                            background-size: 100%;
                            width: 27.656%;
                            height: 18.63vh;
                            margin: auto
                        }
                        
                         .p1_words {
                            font-size: 2.134rem;
                            position: absolute;
                            right: 0;
                            bottom: 30px;
                            left: 0;
                            text-align: center;
                            color: 231815
                        }
                        
                        @-webkit-keyframes p1_lantern {
                            0% {
                                opacity: .5;
                                -webkit-transform: scale(0.8, 0.8);
                                transform: scale(0.8, 0.8)
                            }
                            100% {
                                opacity: 1
                            }
                        }
                        
                        @keyframes p1_lantern {
                            0% {
                                opacity: .5;
                                -webkit-transform: scale(0.8, 0.8);
                                transform: scale(0.8, 0.8)
                            }
                            100% {
                                opacity: 1
                            }
                        }

                     `
                }</style>
            </div>
        )
    }
}
