import React from 'react';
// import bg from './p1_bg.jpg';
import bg from './7b8d0df5988b9c33f5ce5135391f0aa6.png';
import itembg from '../../style/img/c205e9d4d2e847bd9920cb7805e8e15a.png';
// import itembg2 from '../../style/img/3d5c977eee79aced93277909df8d9564.png';
import itembg3 from '../../style/img/7f3326e2f49ce141d4a82f2026048ebf.png';
import itembg4 from '../../style/img/827d080e3049fad32ba7f5391e7708ef.png';
// import itembg5 from '../../style/img/57258be6404ed3042edcf9a40ad66e9b.png';
import itembg6 from '../../style/img/2b24be3264814e61cf94f88f03862525.png';
import itembg7 from '../../style/img/3c2ffba52529643eb47a22ab895aa12f.png';
import itembg8 from '../../style/img/6c1755536bd23451ed1d4b8b8f310429.png';
import itembg9 from '../../style/img/97f9929654629471e0511b63e2bf3a2d.png';
import itembg10 from '../../style/img/9996bc586aae272652b54189070fc434.png';
import itembg12 from '../../style/img/a1b64cee5fadea8817088ad49ac31e84.png';
const arr = [itembg,itembg3,itembg4,itembg6,itembg7,itembg8,itembg9,itembg10,itembg12];
export default class index extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isVisible: false,
            index:0
        }
    }

    componentDidMount() {
        setTimeout(() => {
            this.setState(() => ({
                isVisible: true
            }));
        }, 1000)
    }
    getItem = ()=>{
        const {play} = this.props;
        return <div
            className="absolute top-0 left-0 right-0 bottom-0 m-auto flex flex-column justify-center items-center animated bounceInDown"
            style={{
                width: 200,
                height: 300,
            }}
        >
            <div className=''
                 style={{
                     width: 200,
                     height: 200,
                     borderRadius: '6px',
                     color: 'white',
                     backgroundImage: `url(${arr[this.state.index]})`,
                     backgroundSize: '100% 100%'
                 }}
            />
            <div className="flex items-center justify-center pointer"
                 style={{
                     width: 60,
                     height: 60,
                     borderRadius: '50%',
                     fontSize: 40,
                     border: '1px solid white',
                     color: 'white'
                 }}
                 onClick={() => {
                     play&&play(this.state.index === (arr.length-1));
                     this.setState(()=>({
                         index:this.state.index+1
                     }));
                 }
                 }
            >
                
            </div>
        </div>
    };
    render() {
        const {className} = this.props;
        return (
            <div className={'full-w full-h relative '+className}
                 style={{backgroundImage: `url(${bg})`, backgroundSize: '100% 100%'}}>
                {this.state.isVisible &&
                <div className="full-w full-h relative" style={{backgroundColor: `rgba(0,0,0,.4)`}}>
                    {this.getItem()}
                </div>
                }
                <style>{
                    `

                     `
                }</style>
            </div>
        )
    }
}
